import React, {Component} from 'react';
// import ImgHeatmap from 'https://image.ibb.co/n1tacx/heatmap.png';
import { Link } from 'react-router-dom';

class Usability extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div style={{
        marginLeft: this.props.isDrawerOpen ? '300px' : '50px', marginTop: '66px',
        maxWidth: '90%', padding: '1% 3% 3% 2%'
      }}>
        <h3 id="main"><b>Introduction To Web Usability:</b></h3>
        <p>Usability means ease of use and the first think that needs to be taken care is the user should not be forced
          to think about what needs to done. As the questions in user's mind increase, the app becomes less usable.
          Self explanatory pages and flexible layout will increase usability.
        </p>
        <p>Below are the few attributes of Usability:</p>
        <div style={{marginLeft: '45px'}}>
          <ul style={{fontSize: 'medium'}}>
            <li> Useful</li>
            <li> Efficient</li>
            <li> Desirable</li>
            <li> Memorable</li>
            <li> Effective</li>
            <li> Delightful</li>
          </ul>
        </div>
        <p>While using web/mobile applications, user scan pages instead of reading them to perform the task in hand.</p>
        <img src="https://image.ibb.co/n1tacx/heatmap.png" alt="heat map" />
        <p>Such <em>Heatmap</em> testing results of Mobile/Web app are very useful to find the areas or keywords which attracts the potential users the most.</p>
        <p>Above Usability Testing result shows that users do not go for the best option, they choose the reasonable options.
        It is a known fact that humans are good at scanning and we use this ability of scanning while performing day to day tasks using web/mobile apps.
        Users realize the importance of scanning when users are unable to perform it while completing a task in hand.
          During this process of scanning, if the questions about the usability of app in user's mind increases, then user will most likely switch to app with better design.
        </p>
        <h4><b>Conventions:</b></h4>
        <p>Conventions play an important role in web usability. Visual hierarchy needs to be clear and prominent. While designing applications, conventions need to be followed unless your idea is better. Self-defined sections of pages and guiding users to achieve their final goal are some conventions that are obvious.
        </p>
        <p>Make your designs visually busy and always make sure that the background noise is minimum.
          Users do not mind clicking a wrong link if it is an easy click. Remove less important text and instructions from all pages and make pages self explanatory as user will loiter through app anyway.</p>
        <img alt="Main Page" style={{width: '55%' }} src="https://image.ibb.co/eQRTEc/mainpage.png"/>
        <h4><b>Navigation:</b></h4>
        <p>Users will stop using your app if they can not navigate through it easily.
        Some users will look for search box and some will browse the site. Breadcrumbs can be used to navigate through pages of the application.
        Home page is significantly important before it acts like a reference page and user can start all over again from that page.
        Always give new user a start like selecting a default active tab so that they can start from somewhere. Make sure the active tab appears different than inactive tabs.
        </p>
        <h4><b>Important points to remember:</b></h4>
        <div style={{marginLeft: '45px'}}>
          <ul style={{fontSize: 'medium'}}>
            <li>Users doubts must be cleared by Home page regaeding the app.</li>
            <li>Tag line must be self explanatory.</li>
            <li>Your app should have persistent navigation.</li>
            <li>User Experience should be common through out the app.</li>
            <li>Design should be accessible to users with accessibility issues.</li>
            <li>There should be a "Skip to Main Content" link at the beginning of each page.</li>
            <li>Never ask unnecessary personal information like credit card details.</li>
            <li>Response time from the server should not be too long.</li>
          </ul>
        </div>

        <h4><b>Next Step:</b></h4>
        <p>Now you are aware of the web/mobile Usability concepts. Let's take a look at <Link className="textDecorationNone" to="/usabilityTesting" replace>Usability Testing.</Link></p>
      </div>
    );
  }
}

export default Usability;



